import React, { useContext, useEffect, Fragment } from 'react';
import Navbar from '../Layout/Navbar';
import BrandName from '../Layout/BrandName';
import { LoadingSpinner } from '../Layout/LoadingSpinner';
import { Link } from 'react-router-dom';
import Repos from '../Repos/Repos';
import GithubContext from '../../Context/github/githubContext';

export default function User({ match }) {
    const githubContext = useContext(GithubContext);
    useEffect(() => {
        githubContext.getUser(match.params.login);
        githubContext.getUserrepos(match.params.login);
        // eslint-disable-next-line
    }, []);

    const {
        name,
        avatar_url,
        company,
        location,
        bio,
        blog,
        login,
        html_url,
        followers,
        following,
        public_repos,
        public_gists,
        hireable,
    } = githubContext.user;

    const { loading } = githubContext;
    if (login === 'SushantPhalke') {
        var issushant = true;
    }

    if (loading) {
        return (
            <>
                <div className='brand-and-nav'>
                    <Navbar home='Home' about='About Me' icon='fa fa-github' />
                    <BrandName title={'GitHub Lense'} />
                </div>
                <div className='user-container'>
                    <LoadingSpinner />
                </div>
            </>
        );
    } else {
        return (
            <>
                <div className='brand-and-nav'>
                    <Navbar home='Home' about='About Me' icon='fa fa-github' />
                    <BrandName title={'GitHub Lense'} />
                </div>
                <Link to='/'>
                    <div className='back2home'>Back To Home</div>
                </Link>
                <div className='user-container'>
                    <div className='user-card-left'>
                        <img
                            src={avatar_url}
                            alt='img is not avilable'
                            className='round-img'
                            style={{ width: '150px' }}
                        />
                        <h3>Name : {name}</h3>

                        {issushant ? (
                            <h3>
                                Username : {login} <br />
                                üî∞Developer of GitHub Lense{' '}
                            </h3>
                        ) : (
                            <h3> Username: {login}</h3>
                        )}
                        <h5>
                            <div className='badge badge-success'>
                                <a
                                    href={`https://github.com/${login}?tab=followers`}
                                >
                                    {' '}
                                    üõ∞{followers} Followers{' '}
                                </a>
                            </div>
                            <br />
                            <div className='badge badge-success'>
                                <a
                                    href={`https://github.com/${login}?tab=following`}
                                >
                                    {' '}
                                    üé¶{following} Following{' '}
                                </a>
                            </div>
                            <br />
                            <div className='badge badge-dark'>
                                Hireable {hireable ? <>: ‚úî </> : <>: ‚ùå </>}
                            </div>
                            <br />
                        </h5>
                        <h4>Location : {location}</h4>
                        <h3>Recent Repositories </h3>
                        <Repos repos={githubContext.repos} />
                    </div>
                    <div className='user-card-right'>
                        <ul>
                            <li>
                                {bio && (
                                    <Fragment>
                                        <h3>Bio</h3>
                                        <p>{bio}</p>
                                    </Fragment>
                                )}
                            </li>
                            <li>
                                {company && (
                                    <Fragment>
                                        <h3>Company</h3>
                                        <p>{company}</p>
                                    </Fragment>
                                )}
                            </li>
                            <li>
                                {blog && (
                                    <Fragment>
                                        <h3>Blog</h3>
                                        <a href={blog} className='links'>
                                            {' '}
                                            <p>{blog}</p>
                                        </a>
                                    </Fragment>
                                )}
                            </li>
                            <li>
                                {public_repos && (
                                    <Fragment>
                                        <h3>Public Repositories</h3>
                                        <p>{public_repos}</p>
                                    </Fragment>
                                )}
                            </li>
                            <li>
                                {public_gists && (
                                    <Fragment>
                                        <h3>Public Gists</h3>
                                        <p>{public_gists}</p>
                                    </Fragment>
                                )}
                            </li>
                            <li>
                                <a
                                    href={html_url}
                                    className='btn btn-dark btn-sm my-2'
                                >
                                    View On Github
                                </a>
                            </li>
                        </ul>
                        <p align='center'>
                            <img
                                width='100'
                                src={`https://github-readme-stats.vercel.app/api/top-langs/?username=${login}&langs_count=10&layout=compact&theme=tokyonight`}
                                alt={`${name} most used Languages`}
                            />
                        </p>
                        <p align='center'>
                            <img
                                width='100'
                                src={`https://github-readme-stats.vercel.app/api?username=${login}&theme=dark&show_icons=true`}
                                alt={`${name} github stats`}
                            />
                        </p>
                    </div>
                </div>
            </>
        );
    }
}
