import React from 'react'
import PropTypes from 'prop-types'

const RepoItem = ({repo}) => {
  return (
    <div className='badge'>
     <a className='links' href={`https://github.com/SushantPhalke/${repo.name}`}>{repo.name} </a>
    </div>
  )
}
RepoItem.propType={
  repo:PropTypes.object.isRequired,
}
export default RepoItem;